article {
    margin-bottom: 3em;
}

article > :last-child,
article:last-child {
    margin-bottom: 0;
}

body > article {
    margin-bottom: 0;
}

#top {
    padding: 10em 0 10em 0;
    text-align: left;
}

    #top .image {
        border-radius: 100%;
        width: 20em;
        height: 20em;
        margin: 0;
    }

        #top .image img {
            border-radius: 100%;
        }

    #top h1 {
        margin-top: 0.35em;
    }

    #top p {
        font-size: 1.5em;
        line-height: 1.75em;
    }

        #top p a {
            color: inherit;
        }

#contact footer {
    font-size: 0.9em;
}


/* Large */

@media screen and (max-width: 1280px) {

        #top {
            padding: 8em 0;
        }

            #top .image {
                width: 24em;
                height: 24em;
                margin: 0;
            }

}

/* Medium */

@media screen and (max-width: 980px) {

        #top {
            text-align: center;
            padding: 5em 0;
        }

            #top .image {
                margin: 0 auto 2em auto;
            }

}

/* Small */

@media screen and (max-width: 736px) {

        article {
            clear: both;
        }

        #top {
            padding: 3em 0;
        }

            #top .image {
                width: 15em;
                height: 15em;
                margin-bottom: 0;
            }

            #top p {
                font-size: 1em;
            }

        #contact footer {
            margin: 0;
        }

}
/* Wrappers */

.wrapper {
    /* background-image: url('images/bg.png'); */
    box-shadow: inset 0px 1px 0px 0px rgba(0, 0, 0, 0.05), inset 0px 2px 3px 0px rgba(0, 0, 0, 0.1);
    padding: 2em 0 2em 0;
    text-align: center;
}

.wrapper.style1 {
    background-image: none;
    background-color: #fff;
}

.wrapper.style2 {
    background-color: #fafafa;
    text-shadow: 1px 1px 0px #fff;
}

.wrapper.style3 {
    background-color: #f4f4f4;
    text-shadow: 1px 1px 0px #fff;
}

.wrapper.style4 {
    background-color: #303030;
    color: #999;
    text-shadow: -1px -1px 0px #181818;
}

.wrapper.style4 h1, .wrapper.style4 h2, .wrapper.style4 h3, .wrapper.style4 h4, .wrapper.style4 h5, .wrapper.style4 h6 {
    color: #fff;
}

.wrapper.style4 form input[type=text],
.wrapper.style4 form input[type=password],
.wrapper.style4 form select,
.wrapper.style4 form textarea {
    border: none;
    background: #282828;
}

.wrapper.style4 form input[type=text]:focus,
.wrapper.style4 form input[type=password]:focus,
.wrapper.style4 form select:focus,
.wrapper.style4 form textarea:focus {
    background: #252525;
}

/* Large */

@media screen and (max-width: 1280px) {

/* Wrappers */

        .wrapper {
            padding: 5em 0 5em 0;
            text-align: center;
        }

            .wrapper.style4 .row-special {
                margin: 2em 0 0 0;
                padding: 2em 0 2em 0;
            }

}

/* Small */

@media screen and (max-width: 736px) {

    /* Wrappers */

        .wrapper {
            padding: 3em 0;
            text-align: center;
        }

}




/* Social */

ul.social {
    cursor: default;
    margin: 0;
    list-style: none;
    padding-left: 0;
}

    ul.social li {
        position: relative;
        display: inline-block;
        margin: 0.25em;
        top: 0;
        padding-left: 0;
    }

        ul.social li a {
            -webkit-transition: top .2s ease-in-out;
            transition: top .2s ease-in-out;
            display: block;
            width: 48px;
            height: 48px;
            border-radius: 6px;
            top: 0;
            position: relative;
        }

            ul.social li a:before {
                -webkit-transition: background-color .2s ease-in-out;
                transition: background-color .2s ease-in-out;
                background-color: #444;
                border-radius: 6px;
                box-shadow: inset 0px 0px 0px 1px #282828, inset 0px 2px 1px 0px rgba(255, 255, 255, 0.1);
                color: #2E2E2E !important;
                display: block;
                font-size: 26px;
                height: 48px;
                line-height: 48px;
                text-align: center;
                outline: 0;
                overflow: hidden;
                text-shadow: 1px 1px 0px rgba(255, 255, 255, 0.1);
                width: 48px;
            }

            ul.social li a.fa-twitter {
                background-color: #2DAAE4;
            }

            ul.social li a.fa-facebook-f {
                background-color: #3C5A98;
            }

            ul.social li a.fa-dribbble {
                background-color: #C4376B;
            }

            ul.social li a.fa-linkedin-in {
                background-color: #006599;
            }

            ul.social li a.fa-tumblr {
                background-color: #51718A;
            }

            ul.social li a.fa-google-plus {
                background-color: #DA2713;
            }

            ul.social li a.fa-github {
                background-color: #FAFAFA;
            }

            ul.social li a.fa-rss {
                background-color: #F2600B;
            }

            ul.social li a.fa-instagram {
                background-color: #E0D7C8;
            }

            ul.social li a.fa-foursquare {
                background-color: #39A3D4;
            }

            ul.social li a.fa-skype {
                background-color: #10BEF1;
            }

            ul.social li a.fa-soundcloud {
                background-color: #FE5419;
            }

            ul.social li a.fa-youtube {
                background-color: #BF2E25;
            }

            ul.social li a.fa-blogger {
                background-color: #FF6501;
            }

            ul.social li a.fa-flickr {
                background-color: #0062DB;
            }

            ul.social li a.fa-vimeo {
                background-color: #4C8AB0;
            }

            ul.social li a:hover {
                top: -5px;
            }

                ul.social li a:hover:before {
                    background-color: transparent;
                }


footer {
    margin: 3em 0 0 0;
}

    footer > p {
        font-size: 1.25em;
    }

#footer {
    background-color: #303030;
    color: #999;
    text-shadow: -1px -1px 0px #181818;
    text-align: center;
    padding: 1em 0 1em 0;
}

/* Small */

@media screen and (max-width: 736px) {
        /* Social */

        ul.social {
            padding: 1em 0.5em 0 0.5em;
        }

            ul.social li {
                margin: 0.5em 0.5em 0.5em 0.5em;
            }

                ul.social li a {
                    top: 0 !important;
                }

                    ul.social li a:before {
                        background-color: transparent !important;
                    }
                    
        footer {
            margin: 2.5em 0 0 0;
        }

            footer > p {
                font-size: 1.25em;
            }

            #contact footer {
				margin: 0;
			}
};
